  import urls from '../../utils/urls.js'
  export default {
    data() {
      return {
        isEdit: false
      }
    },
    onShow() {
      let id = JSON.parse(uni.getStorageSync("userInfo")).id
			// let id = '';
			// if(this.isLogin){
			// 	id = JSON.parse(uni.getStorageSync('userInfo')).id
			// }else{
			// 	 id = 0
			// }
      uni.request({
        url: urls.HTTP + '/user/usertopinfo',
        method: 'POST',
        data: {
          id: id
        },
        success: (res) => {
          // console.log(res)
          this.$store.dispatch('userInfo', res.data.user)
          uni.setStorageSync('user', res.data.user)
        }
      })
    },
    methods: {
      changeEdit: function (e) {
        // this.userInfo.intro = e.detail.value
        this.isEdit = false
        let id = JSON.parse(uni.getStorageSync("userInfo")).id
				// let id = '';
				// if(this.isLogin){
				// 	id = JSON.parse(uni.getStorageSync('userInfo')).id
				// }else{
				// 	 id = 0
				// }
        uni.request({
          url: urls.HTTP + '/user/edituser',
          method: 'POST',
          data: {
            id: id,
            introduce: e.detail.value
          },
          success: (res) => {
            // console.log('个人简介', res)
            this.userInfo.intro = res.data.user.introduce
          }
        })
      },
      clearStorage: function (e) {
        uni.showModal({
          title: '提示',
          content: '是否清除全部缓存？需要重新登录',
          success: (res) => {
            if (res.confirm) {
              uni.clearStorageSync()
               uni.reLaunch({
                url: '../common/login'
              })
							// uni.showToast({
              //   title: '全部清除啦'
              // })
            }
          }
        })
      },
			clearStorage1: function (e) {
        uni.showModal({
          title: '提示',
          content: '是否清除全部缓存？',
          success: (res) => {
            if (res.confirm) {
              uni.clearStorageSync()
               uni.navigateTo({
                url: '../common/welcome'
              })							
            }
          }
        })
      },
      goLogin: function () {

         if (!uni.getStorageSync('openid')) {
          this.showDialog = true
        } else if (!JSON.parse(uni.getStorageSync("userInfo")).id) {
          uni.redirectTo({
            url: '../common/register'
          })
        }















      },
			goLogin1(){
				uni.navigateTo({
				  url: '../common/login'
				})
			},
      imgUrl: function (url) {
        return urls.UrlHttp + url
      }
    },
    computed: {
      isLogin () {
        if (!this.$store.state.isLogin) {
          return false
        } else {
          return true
        }
      },
      userInfo () {
        return this.$store.state.userInfo
      }
    }
  }
